<template>
  <page
    @on-ready="onReady"
    ref="page"
    class="me-page common-page"
    :headerHeightVW="headerHeightVW"
    scrollbar
  >
    <van-nav-bar id="header" slot="header" title="设备详情" left-arrow @click-left="onClickLeft"></van-nav-bar>
    <div class="ed-content">
      <div class="information">
        <div class="information-title">设备信息</div>
        <van-cell-group>
          <van-field v-model="deviceForm.name" label="设备名称:" disabled input-align="right" />
          <van-field v-model="deviceForm.serial" label="设备编号:" disabled input-align="right" />
          <van-field v-model="deviceForm.professional" label="所属专业:" disabled input-align="right" />
          <van-field
            v-model="deviceForm.classification"
            label="设备分类:"
            disabled
            input-align="right"
          />
          <van-field v-model="deviceForm.location" label="设备位置:" disabled input-align="right" />
          <van-field
            v-model="deviceForm.specifications"
            label="规格编号:"
            disabled
            input-align="right"
          />
          <van-field v-model="deviceForm.number" label="数量:" input-align="right" disabled />
          <van-field v-model="deviceForm.status" label="设备状态:" input-align="right" disabled />
          <van-field
            v-for="item in compositionDevice"
            :key="item.id"
            v-model="item.name"
            right-icon="arrow"
            disabled
            @click="onTap(item.id)"
            input-align="left"
          />
        </van-cell-group>
      </div>

      <div class="supplier">
        <div class="supplier-title">供货厂家</div>
        <van-cell-group>
          <van-field v-model="supplierForm.production" label="生产厂商:" disabled input-align="right" />
          <van-field v-model="supplierForm.supply" label="供货厂商:" disabled input-align="right" />
          <van-field v-model="supplierForm.integration" label="集成厂商:" disabled input-align="right" />
          <van-field v-model="supplierForm.number" label="出场编号:" disabled input-align="right" />
          <van-field v-model="supplierForm.date" label="出场日期:" disabled input-align="right" />
          <van-field v-model="supplierForm.startDate" label="开始使用日期:" disabled input-align="right" />
          <van-field v-model="supplierForm.startTime" label="投用时间:" input-align="right" />
          <van-field v-model="supplierForm.life" label="使用寿命:" input-align="right" />
          <van-field
            v-model="supplierForm.startExpirationDate"
            label="开始保质日期:"
            input-align="right"
          />
          <van-field v-model="supplierForm.expirationDate" label="质保期限:" input-align="right" />
        </van-cell-group>
      </div>

      <div class="operator">
        <div class="operator-title">运营单位</div>
        <van-field v-model="operatorForm.unit" label="使用单位:" disabled input-align="right" />
        <van-field v-model="operatorForm.department" label="使用部门:" disabled input-align="right" />
        <van-field
          v-model="operatorForm.maintenanceUnit"
          label="维修单位:"
          disabled
          input-align="right"
        />
        <van-field
          v-model="operatorForm.maintenanceDepartment"
          label="维修部门:"
          disabled
          input-align="right"
        />
      </div>
    </div>
  </page>
</template>

<script>
import Page from "../../components/Page";
import navigate from "../../utils/navigate.js";
import io from "../../api";

export default {
  components: {
    Page
  },
  data: () => ({
    headerHeightVW: 0,
    deviceForm: {
      name: "",
      serial: "",
      professional: "",
      classification: "",
      location: "",
      specifications: "",
      number: "",
      status: ""
    },
    supplierForm: {
      production: "",
      supply: "",
      integration: "",
      number: "",
      date: "",
      startDate: "",
      startTime: "",
      life: "",
      startExpirationDate: "",
      expirationDate: ""
    },
    operatorForm: {
      unit: "",
      department: "",
      maintenanceUnit: "",
      maintenanceDepartment: ""
    },
    compositionDevice: []
  }),
  methods: {
    onReady(header, main) {
      this.headerHeightVW = ((header.h + 1) * 100) / header.w;
      const id = navigate.getParam("id");
      io.getDeviceDetails({ id })
        .then(data => {
          this.supplierForm = data.supplierForm;
          this.operatorForm = data.operatorForm;
          this.deviceForm = data.deviceForm;
          this.compositionDevice = data.compositionDevice;
        })
        .catch(msg => {
          this.$toast.clear();
          this.$toast(msg);
        });
    },
    onClickLeft() {
      navigate.back();
    },
    onTap(id) {
      navigate.open("composition_equipment_details", { id });
    }
  }
};
</script>


